body,
html {
    padding: 0px;
    margin: 0px;
}
body {
    width: 100%;
}
.chevrnon-scroll {
    position: absolute;
    top: 98%;
    left: 50%;
    cursor: pointer;
    transform: translate(-50%, -95%);
    animation: up-down 0.8s ease forwards infinite;
}
@keyframes up-down {
    from {
        top: 97%;
    }
    to {
        top: 98%;
    }
}
#root {
    width: 100%;
    height: 100%;
}
.button-icon-dark {
    color: white;
}
.section {
    width: 100%;
    height: 86vh;
}
.welcome-text {
    z-index: 1;
}
.welcome-text-description {
    width: 30%;
    z-index: 1;
}
.backdrop {
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.6;
}
.home {
    position: relative;
    height: 94.3vh;
    background-size: 100% 100%;
}
@media only screen and (max-width: 768px) {
    .welcome-text-description {
        width: 80%;
    }
    .home {
        background-size: 2000px 900px;
        background-position-x: -82%;
    }
}
